// interface
export interface AppState {
  pageTitle: string,
}

// ユーザー一覧取得 アクションクリエイター
export const PAGE_TILE = "app/PAGE_TITLE";

// initial state
const initialState: AppState = { pageTitle: "" };

// reducer
const reducer = (state = initialState, action: any) => {
  switch (action.type) {
    case PAGE_TILE:
      return { ...state, pageTitle: action.payload }
    default:
      return state
  }
};

export default reducer;