  /**
   * メール送信
   * @param message メッセージ
   * @returns レスポンス
   */
  static sendMail(message: MailMessage): Observable<AjaxResponse<any>> {
    //console.log("start send");
    return ajax.post(`${AwsApiClient.URL_BASE}/mail/send`,
      message, 
      { 
        'Content-Type': 'application/json' 
      })
  }

